* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'opens sans', sans-serif;
  font-size: 20px;
}


div.app {
    display: flex;
    flex-direction: column;
    min-width: 100vh;
    width: 90%;
    margin: 0 auto;
}

header {
    flex-basis: 30vh;
    position: relative;
    overflow: hidden;    
}

main {
    display: flex;
    flex-grow: 1;
    padding: 30px 0;
    flex-basis: 60vh;
}

asite {
    min-width: 240px;

}

section.page{
    flex-grow: 1;
}

footer {
    background-color: black;
    color: white;
    padding: 10px 0;
    text-align: center;
    text-transform: uppercase;
}
header img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
}
nav.main {
    padding: 0 30px 30px 0;
}

.main ul {
    list-style-type: none;
}

nav.main a{
    display: block;
    text-decoration: none;
    color: black;
    text-transform: uppercase;
    font-weight: bold;
    padding: 10px;
    transition: .2s;
}

nav.main a:hover{
    transform: translate(20px);
}

nav.main a.active {
    border-left: 10px grey solid;
    transform: translate(0px);
}

nav.main li:nth-child(1) a.active {
    border-left: 10px green solid;
    transform: translate(0px);
}

nav.main li:nth-child(2) a.active {
    border-left: 10px blue solid;
    transform: translate(0px);
}

nav.main li:nth-child(3) a.active {
    border-left: 10px orange solid;
    transform: translate(0px);
}

nav.main li:nth-child(4) a.active {
    border-left: 10px red solid;
    transform: translate(0px);
}


.contact textarea{
    font-size: 16px;
    width: 80%;
    min-height: 200px;
    margin: 20px 0;
    line-height: 1.6;
}

.contact button {
    display: block;
    border: 2px solid black;
    background-color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 20px;
    text-transform: uppercase;
    margin: 0 20% 0 auto;
    transition: .2s;
    font-weight: bold;
}

.contact button:hover {
    color: yellow;
    background-color: black;
}
.products h2{
    margin-bottom: 20px;

}

.products ul {
    list-style: square;
    padding-left: 30px;

}

.products li {
    margin-bottom: 20px;
}

.products a{
    text-decoration: none;
    color: #333;
}

.products a:hover{
    text-decoration: underline;
    color: black;
}
footer span{
    color: red;

}

footer p{
    font-size: 14px;
}
